export default {
  props: {
    value: Object,
    name: String,
    email: String,
    owner: String
  },
  mounted () {
    this.name = this.value.name
    this.email = this.value.email
    this.owner = this.value.owner
  },
  computed: {
    lastName () {
      let length = this.value.name.length
      return this.value.name.substring(length - 1)
    },
    ownerString () {
      switch (this.value.owner) {
        case 'manager':
          return '管理者'
        default:
          return '浏览者'
      }
    }
  }
}



